html,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.myContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

#quote-box {
  background: whitesmoke;
  width: 50vw;
  height: 40vh;
  border-radius: 10px;
  text-align: center;
  position: relative;
}

/* quote styling */

#text {
  margin-top: 5em;
  font-size: 1.2em;
}

/* Button styling */
#tweet-quote {
  position: absolute;
  bottom: 1em;
  left: 1em;
}

#new-quote {
  position: absolute;
  bottom: 1em;
  right: 1em;
}
#author {
  position: absolute;
  right: 2em;
}

@media (max-width: 412px) {
  #text {
    font-size: 1.2em;
    color: red;
  }
  #quote-box {
    background: whitesmoke;
    width: 100vw;
    height: 60vh;
    border-radius: 10px;
    text-align: center;
    position: relative;
  }
  #author {
    font-size: 1em;
  }

  #new-quote,
  #tweet-quote {
    font-size: 1em;
    min-width: 2em;
    color: blue;
  }
}

@media (max-width: 1023px) {
  #text {
    font-size: 1.2em;
  }
  #quote-box {
    background: whitesmoke;
    width: 80vw;
    height: 50vh;
    border-radius: 10px;
    text-align: center;
    position: relative;
  }
  #author {
    font-size: 1.2em;
  }

  #new-quote,
  #tweet-quote {
    font-size: 1.2em;
    width: 10x;
    color: blue;
  }
}
